
export const ul = {
  marginTop: 0,
};

export const listGroup = Object.assign(ul, {
  display: 'flex',
  flexDirection: 'column',
  paddingLeft: 0,
  marginBottom: 0,
});

export const listGroupItem = {
  position: 'relative',
  display: 'block',
  padding: 0,
  marginBottom: -1,
  backgroundColor: '#fff',
  border: '1px solid rgba(0,0,0,.125)',
};

export const blockLink = {
  textDecoration: 'none',
  width: '100%',
  padding: '0.75rem 1.25rem',
  fontSize: 15,
  display: 'inline-block',
};



